import './App.css';
import Radio from './components/Radio';
import MoneyRain from './components/MoneyRain';
import DayNightToggle from 'react-day-and-night-toggle'
import { useState } from 'react';


import bill1 from './assets/bill1.png';
import bill2 from './assets/bill2.png';
import bill3 from './assets/bill3.png';

function App() {

    const [isDarkMode, setIsDarkMode] = useState(localStorage.getItem('data-theme') === 'dark' ? true : false)

    const moneyImages = [bill1, bill2, bill3];

    window.matchMedia('(prefers-color-scheme: dark)').addEventListener('change', e => {
      const newColorScheme = e.matches ? 'dark' : 'light'
  
      setIsDarkMode(newColorScheme === 'dark' ? true : false)
      localStorage.setItem('data-theme', newColorScheme)
      document.body.setAttribute('data-theme', localStorage.getItem('data-theme'))
    })
  
    const handleChangeTheme = () => {
      setIsDarkMode(!isDarkMode)
      if(!isDarkMode) {
        localStorage.setItem('data-theme', 'dark')
        document.body.setAttribute('data-theme', 'dark')
      } else {
        localStorage.setItem('data-theme', 'light')
        document.body.setAttribute('data-theme', 'light')
      }
    }

  return (
    <div className="App">
    <div className="banner">
     <MoneyRain />
    <h1>SEARCH RADIO</h1>
    <DayNightToggle
      onChange={handleChangeTheme}
      checked={isDarkMode}
      className="dark"
      size={15}
    />
    </div>
    <Radio />
</div>
  );
}

export default App;
